*,
*::before,
*::after {
    box-sizing: border-box;
}

.header1-text {
    font-size: 25px;
    color: #90CDF9;
    font-weight: 500;
}

.header2-text {
    font-size: 14px;
    font-weight: 500;
}

.header3-text {
    font-size: 12px;
    font-weight: 500;
}

.base-text {
    font-size: 12px;
}

.help-text {
    font-size: 12px;
    color: #999;
}

.disabled-text {
    font-size: 12px;
    color: #ccc;
}

.scheduler {
    margin: 20px auto;
    border-spacing: 0;
}

.scheduler td {
    padding: 0px;
}

.expander-space {
    overflow: hidden;
    display: inline-block;
    width: 1em;
    height: 1em;
    line-height: 1em;
    font-size: .9em;
    vertical-align: middle;
    margin-top: -1%;
}

.resource-view {
    border: 1px solid #e9e9e9;
    overflow: hidden;
}

.scheduler-view {
    border: 1px solid #e9e9e9;
    margin: 0 0 0 -1px;
    padding: 0;
}

.scheduler-content {
    position: relative;
    z-index: 2;
}

.scheduler-bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
}

table.resource-table,
table.scheduler-bg-table,
table.scheduler-table {
    width: 100%;
    margin: 0;
    padding: 0;
    border-spacing: 0;
    text-align: center;
    height: fit-content;
}

table.scheduler-table {
    /* border: 1px solid #e9e9e9; */
}

table.scheduler-content-table {
    margin: 0;
    padding: 0;
    /* border: 0 solid #e9e9e9; */
    border-spacing: 0;
}

table.resource-table tr,
table.scheduler-bg-table tr,
table.scheduler-table tr {
    /* border-bottom: 1px solid #e9e9e9; */

    background-color: #fff;

}

table.resource-table th,
table.scheduler-table th,
table.resource-table td,
table.scheduler-bg-table td,
table.scheduler-table td {
    border-right: 1px solid #e9e9e9;
    /* border-bottom: 1px solid #e9e9e9; */
}

table.scheduler-bg-table th {
    border-right: 1px solid #e9e9e9;
    background-color: #ffffff;
}

table.resource-table tr th:last-child,
/*table.scheduler-bg-table tr th:last-child, */
table.scheduler-table tr th:last-child,
table.resource-table tr td:last-child,
/*table.scheduler-bg-table tr td:last-child, */
table.scheduler-table tr td:last-child {
    border-right: 0
}

/*table.resource-table tr:last-child td, table.scheduler-bg-table tr:last-child td, */
table.scheduler-table tr:last-child td {
    border-bottom: 0
}

.timeline-event {
    position: absolute;
    cursor: pointer;
}

.day-event {
    position: relative;
    display: inline-block;
    margin: 0px 5px;
}

.day-event-container {
    text-align: left;
    padding: 5px 5px 0 5px;
}

.round-all {
    border-radius: 14px;
}

.round-head {
    border-radius: 14px 0px 0px 14px;
}

.round-tail {
    border-radius: 0px 14px 14px 0px;
}

.round-none {
    border-radius: 0px;
}

.event-container {
    position: relative;
}

.border-cell {
    border-left: 1px solid #eeeeee;
    border-right: 1px solid #eeeeee;
}

.event-item {
    margin: 1px 0;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: #fff;
    padding-right: 20px !important;
    /* margin-bottom: 4rem; */
    height: 5rem;
    opacity: 0.5;
    border-radius: 0.5rem;
}

.overflow-text {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-right: 5px !important;
}

.status-dot {
    width: 14px;
    height: 14px;
    border-radius: 7px;
}

.ant-radio-button-wrapper-checked {
    background-color: #108EE9;
    color: #FFFFFF;
}

.icon-nav:hover {
    color: #1E90FF !important;
    box-shadow: 0 0 0px !important;
    cursor: pointer;
}

.add-more-popover-overlay {
    position: absolute;
    z-index: 5;
    border: 1px solid #e5e5e5;
    background-color: #fff;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.25);
    padding: 10px;
}

.popover-calendar {
    width: 300px;
}

.popover-calendar .ant-calendar {
    box-shadow: 0 1px 6px rgba(0, 0, 0, 0) !important;
}

.event-resizer {
    position: absolute;
    z-index: 4;
    display: block;
    width: 7px;
    top: -1px;
    bottom: -1px;
}

.event-start-resizer {
    cursor: w-resize;
    left: -1px;
}

.event-end-resizer {
    cursor: e-resize;
    right: -1px;
}

.selected-area {
    position: absolute;
}

.slot-cell {
    /* padding-left: 4px;
    padding-right: 4px; */
}

.slot-text {
    display: inline-block;
    padding-left: 4px;
    padding-right: 4px;
}

.btn {
    background: #f6f6f6;
    background-image: -webkit-linear-gradient(top, #f6f6f6, #eeeeee);
    background-image: -moz-linear-gradient(top, #f6f6f6, #eeeeee);
    background-image: -ms-linear-gradient(top, #f6f6f6, #eeeeee);
    background-image: -o-linear-gradient(top, #f6f6f6, #eeeeee);
    background-image: linear-gradient(to bottom, #f6f6f6, #eeeeee);
    -webkit-border-radius: 4;
    -moz-border-radius: 4;
    border-radius: 4px;
    /* font-family: Arial; */
    color: #666666;
    font-size: 1rem;
    /* padding: 10px 20px 10px 20px; */
    border: solid #bbb 1px;
    text-decoration: none;
}

.btn-selected {
    background: #d0d0d0;
    background-image: -webkit-linear-gradient(top, #d0d0d0, #d8d8d8);
    background-image: -moz-linear-gradient(top, #d0d0d0, #d8d8d8);
    background-image: -ms-linear-gradient(top, #d0d0d0, #d8d8d8);
    background-image: -o-linear-gradient(top, #d0d0d0, #d8d8d8);
    background-image: linear-gradient(to bottom, #d0d0d0, #d8d8d8);
    -webkit-border-radius: 4;
    -moz-border-radius: 4;
    border-radius: 4px;
    /* font-family: Arial; */
    color: #666666;
    font-size: 1rem;
    /* padding: 10px 20px 10px 20px; */
    border: solid #666666 1px;
    text-decoration: none;
}

.scheduler-header {
    background: #d0d0d0;
    background-image: -webkit-linear-gradient(top, #eeeeee, #e4e4e4);
    background-image: -moz-linear-gradient(top, #eeeeee, #e4e4e4);
    background-image: -ms-linear-gradient(top, #eeeeee, #e4e4e4);
    background-image: -o-linear-gradient(top, #eeeeee, #e4e4e4);
    background-image: linear-gradient(to bottom, #eeeeee, #e4e4e4);
    text-decoration: none;
}

.btn:hover {
    background: #f6f6f6;
    background-image: -webkit-linear-gradient(top, #f6f6f6, #eeeeee);
    background-image: -moz-linear-gradient(top, #f6f6f6, #eeeeee);
    background-image: -ms-linear-gradient(top, #f6f6f6, #eeeeee);
    background-image: -o-linear-gradient(top, #f6f6f6, #eeeeee);
    background-image: linear-gradient(to bottom, #f6f6f6, #eeeeee);
    text-decoration: none;
}

.stickyCell {
    position: sticky !important;
    left: 0 !important;
    z-index: 10 !important;

    /* &--header {
        @extend .stickyCell;
        z-index: 20 !important; // Header cell need higher z-index then row cells to show on top of them when scrolled vertically
    } */
}

.projectselctor {
    height: 2rem;
    border: 1px solid #eee;
    list-style-type: inherit;
    font-size: 1rem;
    font-weight: 600;
    padding-left: 1rem;
    height: 2.2rem;
}

.projectselctor:focus {
    height: 2rem;
    border: 1px solid #eee;
    list-style-type: inherit;
}

input::-webkit-calendar-picker-indicator {
    opacity: 100;
    background-image: -webkit-linear-gradient(top, #eeeeee, #e4e4e4);
    background-image: -moz-linear-gradient(top, #eeeeee, #e4e4e4);
    background-image: -ms-linear-gradient(top, #eeeeee, #e4e4e4);
    background-image: -o-linear-gradient(top, #eeeeee, #e4e4e4);
    background-image: linear-gradient(to bottom, #eeeeee, #e4e4e4);
    border: 1px solid #eeeeee;
    display: flex;
    justify-content: center;
    align-self: center;
    padding-top: 1rem;
    height: 2.2rem !important;
}

input::-webkit-calendar-picker-indicator:active {
    opacity: 100;
    background-image: -webkit-linear-gradient(top, #eeeeee, #e4e4e4);
    background-image: -moz-linear-gradient(top, #eeeeee, #e4e4e4);
    background-image: -ms-linear-gradient(top, #eeeeee, #e4e4e4);
    background-image: -o-linear-gradient(top, #eeeeee, #e4e4e4);
    background-image: linear-gradient(to bottom, #eeeeee, #e4e4e4);
    border: 1px solid #eeeeee;
    display: flex;
    justify-content: center;
    align-self: center;
    padding-top: 1rem;
    height: 2.2rem !important;
}

.availabilitySelector:hover {
    background-color: #e1e1e1;
}